import axios from 'axios';
import store from '@/store';
import router from '@/router';

const errorHandler = (status, msg) => {
    switch (status) {
        case 400:
            break;
        case 401:
            store.dispatch('auth/setAuth', {
                'token': '',
                'isLogin': false,
            });
            setTimeout(() => {
                router.replace('/');
            }, 1000)
            break;
        case 403:
            break;
        case 404:
            break;
        case 409:
            break;
        default:
            console.log(`Error: ${msg}`)
    }
}

const host = 'http://127.0.0.1:5000';
const api = axios.create({
    baseURL: `${host}/api`,
    headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
    }
});


api.interceptors.request.use(
    config => {
        // Do something before request is sent
        const access_token = store.state.auth.token;
        if (access_token) {
            config.headers['Authorization'] = `Bearer ${access_token}`;
        }
        return config;
    },
    function (error) {
        // Do something with request error
        return Promise.reject(error);
    }
);

api.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        const { response } = error;
        if (response) {
            errorHandler(response.status, response.data.error);
            return Promise.reject(error);
        }
    }
)

export default function (method, url, data = null) {
    method = method.toLowerCase();
    if (method === 'post') {
        return api.post(url, data);
    } else if (method === 'get') {
        return api.get(url, { params: data });
    } else if (method === 'delete') {
        return api.delete(url, { params: data });
    } else if (method === 'patch') {
        return api.patch(url, data);
    } else {
        console.error(`Invalid method: ${method}`);
    }
}