import req from './http.js'

export const createTask = params => req('post', '/tasks', params);
export const getTasks = () => req('get', '/tasks');
// export const getTask = id => req('get', `/task/${id}`);
export const updateTask = (id, params) => req('patch', `/task/${id}`, params);
export const deleteTask = id => req('delete', `/task/${id}`);
