<template>
  <v-container key="center">
    <v-data-table
      :headers="headers"
      :items="tasks"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>Tasks</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                New task
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.title"
                        label="Title"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.content"
                        label="Content"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-checkbox
                        v-model="editedItem.done"
                        label="Done"
                      ></v-checkbox>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-menu
                        ref="dateMenu"
                        v-model="dateMenu"
                        :close-on-content-click="false"
                        :return-value.sync="editedItem.expire_date"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="editedItem.expire_date"
                            label="Expire Date"
                            prepend-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="editedItem.expire_date"
                          no-title
                          scrollable
                        >
                          <v-spacer></v-spacer>
                          <v-btn
                            text
                            color="primary"
                            @click="dateMenu = false"
                          >
                            Cancel
                          </v-btn>
                          <v-btn
                            text
                            color="primary"
                            @click="$refs.dateMenu.save(editedItem.expire_date)"
                          >
                            OK
                          </v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-menu
                        ref="timeMenu"
                        v-model="timeMenu"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        :return-value.sync="editedItem.expire_time"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="editedItem.expire_time"
                            label="Expire Time"
                            prepend-icon="mdi-clock-time-four-outline"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-time-picker
                          v-if="timeMenu"
                          v-model="editedItem.expire_time"
                          full-width
                          @click:minute="$refs.timeMenu.save(editedItem.expire_time)"
                        ></v-time-picker>
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-fab-transition>
      <v-btn
        fab
        dark
        bottom
        right
        fixed
        @click="logout"
      >
        <v-icon>mdi-logout</v-icon>
      </v-btn>
    </v-fab-transition>
  </v-container>
</template>

<script>
  import {createTask, getTasks, updateTask, deleteTask} from '@/apis/task.js'
  export default {
    name: 'Task',
    
    data: () => ({
      dialog: false,
      dialogDelete: false,
      // date: ,
      dateMenu: false,
      timeMenu: false,
      headers: [
        {
          text: 'Title',
          align: 'start',
          value: 'title',
        },
        { text: 'Content', value: 'content' },
        { text: 'Done', value: 'done' },
        { text: 'Expire at', value: 'expire_at' },
        { text: 'Update at', value: 'update_at' },
        { text: 'Create at', value: 'create_at' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      tasks: [],
      editedIndex: -1,
      editedItem: {
        title: '',
        content: '',
        done: false,
        expire_date: '',
        expire_time: '',
      },
      defaultItem: {
        title: '',
        content: '',
        done: false,
        expire_date: '',
        expire_time: '',
      },
      rules: [v => !!v || 'This field is required', v => v.length <= 24 || 'Max 24 characters', v => v.length >= 8 || 'Min 8 characters'],
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Task' : 'Edit Task'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },
    mounted () {
      getTasks().then((response) => {
        this.tasks = response.data.data;
      })
    },
    methods: {
      editItem (item) {
        item['expire_date'] = item['expire_at'].substr(0, 10);
        item['expire_time'] = item['expire_at'].substr(11, 5);
        this.editedIndex = this.tasks.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.tasks.indexOf(item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        deleteTask(this.tasks[this.editedIndex].tid).then((response) => {
          console.log(response)
        })
        this.tasks.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          let tmp = {
            title: this.editedItem.title,
            content: this.editedItem.content,
            done: this.editedItem.done,
            expire_at: `${this.editedItem.expire_date} ${this.editedItem.expire_time}`,
          }
          updateTask(this.tasks[this.editedIndex].tid, tmp).then((response) => {
            console.log(response)
          })
          let timeStr = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString()
          let timeStrFormat = `${timeStr.substr(0, 10)} ${timeStr.substr(11, 5)}`
          tmp['update_at'] = timeStrFormat;
          Object.assign(this.tasks[this.editedIndex], tmp)
        } else {
          let tmp = {
            title: this.editedItem.title,
            content: this.editedItem.content,
            done: this.editedItem.done,
            expire_at: `${this.editedItem.expire_date} ${this.editedItem.expire_time}`,
          }
          createTask(tmp).then((response) => {
            console.log(response)
          })
          let timeStr = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString()
          let timeStrFormat = `${timeStr.substr(0, 10)} ${timeStr.substr(11, 5)}`
          tmp['create_at'] = timeStrFormat;
          tmp['update_at'] = timeStrFormat;
          this.tasks.push(tmp)
        }
        
        this.close()
      },
      logout () {
        this.$store.dispatch('auth/setAuth', {
            'token': null,
            'isLogin': false,
        });
        this.$router.push('/');
      },
    }
  }
</script>
