<template>
  <v-container key="center">
    <v-row justify="center" align="center">
      <v-col
        class="mb-5"
        cols="6"
      >
        <v-card
          elevation="2"
          outlined
        >

          <v-toolbar
            color="blue darken-2"
            dark
          >
            <v-toolbar-title>
              Sign in
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-row>
              <v-icon style="width: 40px;">mdi-account</v-icon>
              <v-text-field
                ref="username"
                v-model="username"
                :rules="rules"
                counter
                label="Username"
                required
              ></v-text-field>
            </v-row>
            <v-row>
              <v-icon style="width: 40px;">mdi-lock</v-icon>
              <v-text-field
                ref="password"
                v-model="password"
                :rules="rules"
                :type="show_password ? 'text' : 'password'"
                counter
                label="Password"
                :append-icon="show_password ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="show_password = !show_password"
                required
              ></v-text-field>
            </v-row>
            <v-btn block style="margin-top: 20px;" @click="login" color="primary">
              Login
            </v-btn>
            <v-btn block style="margin-top: 20px;" @click="signup">
              Sign up
            </v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar
      v-model="msgShow"
    >
      {{ msg }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue darken-2"
          text
          v-bind="attrs"
          @click="msgShow = false; msg=''"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
  import {apiAuth, apiRegistration} from '@/apis/auth.js'

  export default {
    name: 'Signin',
    
    data: () => ({
      username: '',
      password: '',
      show_password: false,
      rules: [v => !!v || 'This field is required', v => v.length <= 24 || 'Max 24 characters', v => v.length >= 8 || 'Min 8 characters'],
      formHasErrors: false,
      msg: '',
      msgShow: false,
    }),
    computed: {
      form () {
        return {
          username: this.username,
          password: this.password,
        }
      },
    },
    methods: {
      showMsg (msg) {
        this.msg = msg;
        this.msgShow = true;
      },
      validateForm () {
        this.formHasErrors = false;

        Object.keys(this.form).forEach(f => {
          if (!this.$refs[f].validate(true)) {
            this.formHasErrors = true;
          }
        })
        return !this.formHasErrors
      },
      login () {
        if (this.validateForm()) {
          apiAuth({
            username: this.username,
            password: this.password,
          }).then(response => {
            console.log(response);
            let token = response.data.data.access_token;
            console.log(token);
            this.showMsg('Login success!');
            this.$store.dispatch('auth/setAuth', {
                'token': token,
                'isLogin': true,
            });
            this.$router.push('/task')
          });
        } else {
          this.showMsg('Field has something error...');
        }
      },
      signup () {
        if (this.validateForm()) {
          apiRegistration({
            username: this.username,
            password: this.password,
          }).then((response) => {
            console.log(response);
          }).catch(() => {
            this.showMsg('409 User already exists...');
          });
        } else {
          this.showMsg('Field has something error...');
        }
      },  
    }
  }
</script>
